import React, { useState, useEffect } from 'react';

import api from '../../services/api';
import splitData from '../../services/splitData';

import './Works.css';

function Works() {

    const [works, setWorks] = useState([]);

    useEffect(() => {
        api.get('works.json')
            .then(response => {
                setWorks(response.data);
            })
    }, []);

    return(
        <section id="Works" className="section">
            <div className="container">
                <h1 className="title is-size-2">Projetos que estou envolvido:</h1>

                {
                    splitData(works, 2).map(worksChunk => (
                    <div className="columns">
                        { worksChunk.map( work => (
                            <div className="column" key={work.id}>
                                <a href={work.url}>
                                    <div className="card">
                                        <div className="card-image">
                                            <figure className="image is-4by3">
                                                <img src={work.img} alt={work.appname} />
                                            </figure>
                                        </div>
                                        <div className="card-content">
                                            <div className="media">
                                                <div className="media-content">
                                                    <p className="title is-4">{work.appname}</p>
                                                    <p className="subtitle is-6">{work.apptype}</p>
                                                </div>
                                            </div>

                                            <div className="content">
                                                {work.description} 
                                                <br />
                                                <br />
                                                {
                                                    work.techs.map(tech => (
                                                        <strong className="has-text-success">{tech + ' '}</strong>
                                                    ))
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </a>
                            </div>
                        ))
                        }
                    </div>
                    ))
                }

            </div>
        </section>
    );
}

export default Works;