import React, { useState, useEffect } from 'react';

import api from '../../services/api';
import splitData from '../../services/splitData';

import './MyWorks.css';

function MyWorks() {

    const [myWorks, setMyWorks] = useState([]);

    useEffect(() => {
        api.get('myworks.json')
            .then(response => {
                setMyWorks(response.data);
            })
    }, []);

    return(
        <section id="MyWorks" className="section">
            <div className="container">
                <h1 className="title is-size-2">Meus projetos pessoais:</h1>

                {
                    splitData(myWorks, 2).map(myWorksChunk => (
                    <div className="columns">
                        { myWorksChunk.map( myWork => (
                            <div className="column" key={myWork.id}>
                                <a href={myWork.url}>
                                    <div className="card">
                                        <div className="card-image">
                                            <figure className="image is-4by3">
                                                <img src={myWork.img} alt={myWork.appname} />
                                            </figure>
                                        </div>
                                        <div className="card-content">
                                            <div className="media">
                                                <div className="media-content">
                                                    <p className="title is-4">{myWork.appname}</p>
                                                    <p className="subtitle is-6">{myWork.apptype}</p>
                                                </div>
                                            </div>

                                            <div className="content">
                                                {myWork.description} 
                                                <br />
                                                <br />
                                                {
                                                    myWork.techs.map(tech => (
                                                        <strong className="has-text-success">{tech + ' '}</strong>
                                                    ))
                                                }
                                            </div>
                                        </div>
                                    </div>
                                </a>
                            </div>
                        ))
                        }
                    </div>
                    ))
                }

            </div>
        </section>
    );
}

export default MyWorks;